body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Message {
  padding: 2rem;
  margin: 1rem;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  text-transform: uppercase;
  border-radius: 10px;
  text-align: center;
}

.Chips {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Chips button {
  padding: 15px;
  border-radius: 8px;
  border: none;
  font-size: 18px;
  text-transform: uppercase;
  background-color: pink;
}

.Chips button:hover {
  background-color: red;
  cursor: pointer;
}

.Chips button:active,
.Chips button:focus {
  outline: none;
}

.bag {
  position: absolute;
  z-index: -1;
}

.Sardines {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Sardines .message {
  padding: 30px;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  text-transform: uppercase;
  border-radius: 10px;
}

.Sardines .message h1 {
  text-align: center;
  color: white;
}

.Soda {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100vh;
}

.Soda img {
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Navbar {
  background: rgba(69, 90, 100, 1);
  width: 100%;
  padding: 1.5rem;
  display: flex;
  justify-content: space-around;
}

.Navbar a {
  text-decoration: none;
  color: rgba(144, 164, 174, 1);
  transition: all 0.5s ease;
}
.Navbar a.Navbar-active {
  color: white;
}

.VendingMachine {
  text-align: center;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.VendingMachine .Message {
  width: 30%;
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

